import { Routes, RouterModule } from '@angular/router';
import {ModuleWithProviders} from "@angular/core";
import { ClientemensalistaComponent } from './clientemensalista.component';

export const clientemensalistaRoutes: Routes = [
    {
        path: '',
        component: ClientemensalistaComponent,
        data: {
            pageTitle: 'Clientemensalista'
        }
    }
]; 

export const clientemensalistaRouting: ModuleWithProviders = RouterModule.forChild(clientemensalistaRoutes);

